import React, { Component } from 'react';
import { Button, Form, Row, Col, Input, Select, Radio, DatePicker } from 'antd';
import Icon from '../icon'
import './style.less';
const { Search } = Input;
const { Option } = Select;
const { RangePicker } = DatePicker;

class AdvancedSearchForm extends Component {
  formRef = React.createRef();
  cols = []
  state = {
    advance: false,
  };

  getComponent(item) {
    switch (item.component) {
      case 'input':
        return <Input placeholder={`请输入${item.title}`} />;
      case 'select':
        return <Select placeholder={item.placeholder} allowClear showSearch>{
          item.options.map((subitem, index) =>
            <Option value={subitem.value} key={index}>{subitem.title}</Option>
          )
        }</Select>;
      case 'date':
        return <RangePicker showTime allowClear />
      default:
        return <Input placeholder={`请输入${item.title}`} />;
    }
  }

  getFields = () => {
    const { columns = [] } = this.props;
    const children = [];
    columns.forEach((item, i) => {
      if (item.component) {
        children.push(
          <Col span={8} key={i}>
            <Form.Item label={item.title || ' '} name={item.dataIndex}>
              {this.getComponent(item)}
            </Form.Item>
          </Col>,
        );
      }
    });
    children.push(
      <Col span={8} key={columns.length}>
        <Form.Item label=" " colon={false}>
          <Button type="primary" htmlType="submit">
            <Icon iconName="Zoom" />查询
          </Button>
          <Button style={{ marginLeft: 15 }} onClick={this.handleReset}>
            重置
          </Button>
        </Form.Item>
      </Col>)
    return children;
  }

  handleSearch = (value) => {
    const onSearch = this.props.onSearch;
    if (typeof onSearch == 'function') {
      let where = {
        _or: this.cols.map(item => {
          let obj = {}
          obj[`${item.dataIndex}_contains`] = value
          return obj
        })
      }
      onSearch(where);
    }
  };

  handleSubmit = (values = {}) => {
    const onSubmit = this.props.onSubmit;
    if (typeof onSubmit == 'function') {
      onSubmit(values);
    }
  };

  handleStatusChange = e => {
    const onStatusChange = this.props.onStatusChange;
    if (onStatusChange) onStatusChange(e.target.value)
  }

  handleReset = () => {
    if (this.formRef) this.formRef.current.resetFields();
  };

  toggle = () => {
    const { advance } = this.state;
    this.setState({ advance: !advance });
  };

  render() {
    const labelCol = {
      xs: { span: 24 },
      sm: { span: 6 }
    };
    let { advance } = this.state
    const { columns = [], status, statusValue, search, showAdvance, data = {} } = this.props;
    this.cols = columns.filter(item => item.forSearch) || [];
    return (
      <Form className="ant-advanced-search-form" onFinish={this.handleSubmit} defaultvalues={data} ref={this.formRef} labelCol={labelCol}>
        <Row gutter={24}>
          {status ? <Col span={search ? 12 : 20}>
            {
              <Form.Item label={``} name="status">
                <Radio.Group value={statusValue} onChange={this.handleStatusChange}>
                  {
                    status.map((item, index) => (
                      <Radio.Button value={item.value} key={index}>{item.key}</Radio.Button>
                    ))
                  }
                </Radio.Group>
              </Form.Item>
            }
          </Col> : null}
          {search ? <Col span={8}>
            <Form.Item label={`搜索`} name="keyword">
              <Search
                placeholder={`请输入${this.cols.map(item => item.title).join('/')}关键字`}
                onSearch={this.handleSearch}
                enterButton
              />
            </Form.Item>
          </Col> : null}
          {(columns.length && showAdvance && search) ? <Col span={4}>
            <Form.Item label={``}>
              <Button type="link" onClick={this.toggle}>
                {advance ? '普通搜索' : '高级搜索'}
                <Icon iconName={advance ? "ChevronUp" : "ChevronDown"} />
              </Button>
            </Form.Item>
          </Col> : null}
        </Row>
        {(advance || showAdvance) ? (
          <Row gutter={24}>{this.getFields()}</Row>
        ) : null}
      </Form>
    )
  }
};
export default AdvancedSearchForm;
